    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, rgba(130, 140, 149, 0.5) 0%, rgba(129, 139, 148, 0.5) 1%, rgba(40, 52, 59, 0.99) 99%, #b5bdc8 100%);
