    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#80828c95', endColorstr='#b5bdc8',GradientType=1 );
    /* IE6-9 fallback on horizontal gradient */ }
  .video .contenido-video {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    justify-content: center;
    line-height: 3rem; }
    .video .contenido-video h1 {
      font-size: 3.5rem;
      text-transform: uppercase;
      font-weight: bold;
      text-align: center; }
    .video .contenido-video hr {
      width: 60%;
      height: 1px;
