      background-image: linear-gradient(to left, #9c9c9c, #292944, #9c9c9c); }
    .video .contenido-video p {
      font-size: 2.8rem;
      font-weight: bold; }
  .video video {
    width: 100%;
    display: block; }

.myself {
  background-color: #323333;
  padding: .2rem; }
  .myself p {
    text-align: center;
    color: #fff; }

.idioma {
