  height: 5rem;
  background-color: #323333;
  color: #6e88c5;
  display: grid;
  place-items: center;
  font-weight: bold;
  border-radius: 50%; }
  .btn-cerrar:hover {
    cursor: pointer; }

.link-site {
  width: 10rem;
  height: 5rem;
  background-color: #2b3f6d;
  color: #fff;
  display: grid;
  place-items: center;
  font-weight: bold;
  text-transform: uppercase; }
  .link-site:hover {
    cursor: pointer; }

.skills {
  background-color: #6e88c5;
  margin: 1rem 0; }
  .skills .skills-contenido {
    padding: 1rem 0; }
